<template lang="pug">           
    .programme__item-content
        .programmes__point-number 
        input(v-model='point.name' rows="2").programmes__point-name
        textarea(v-model='point.description').programmes__point-desc
        input(type='number' v-model='point.hours').programmes__point-duration
        button(@click='savePoint()').new__programme-point-add
        button(@click.prevent='closeNewPoint').new__programme-point-reset
</template>
<script>
import { mapActions } from "vuex";

export default {
    data() {
        return {
            point: {
                name: '',
                description: '',
                hours: 0
            }
        }
    },
    props: {
        programmeId: Number
    },
    methods: {
        ...mapActions("programmePoints", ["addProgrammePoint"]),
        savePoint() {
            this.$emit('addNewPoint')
            var data = {
                id: this.programmeId,
                point: this.point
            }
            this.addProgrammePoint(data)
        },
        closeNewPoint() {
            this.$emit('closeNewPoint')
        }
    }
}
</script>
